.btn{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 200px;
    height: 80px;
    background-color: $blue;
    font-size: 20px;
    border-radius: 10px;
    color: $white;
    border: 2px solid $blue2;

    &:hover {
        background-color: $blue2;
    }

    &:active {
        background-color: $blue3;
    }

    &--form {
        margin: 50px auto 0 auto;
        width: 100px;
        height: 50px;
   

    }

    &__low {
        width: 50%;
        height: 60px;
    }

    &--left {
        margin-left: 5%;
    }
    &--right{
        margin-left: 5%;
    }

    @media only screen and(max-width: 400px) {

        
       &--left {
           margin-left: 0;
           margin-bottom: 25px;
       }
       &--right{
        margin-left: 0;
        margin-bottom: 50px;
    }
 
     
      }
      @media only screen and(max-width: 600px) {

        
      
     &__low {
         width: 90%;
         height: 50px;
         margin: 10px 0;
     }
      
       }
  

}

.flex__btn {
    display: flex;
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 100px;
    padding-bottom: 50px;
    background-color: $white;

    
    @media only screen and(max-width: 600px) {

        
        flex-direction: column;
        align-items: center;
        padding-top: 50px;

      
       }


    @media only screen and(max-width: 400px) {

        
        flex-direction: column;
        align-items: center;

      
       }
   
}