#statistics{
    display: none;

    h1 {
        padding-top: 60px;
        padding-bottom: 40px;
    }

    #diagramCzujnik {
        display: none;

    }

    #diagramUrzadzenia {
        display: none;

    }

    .guziki {
        display: flex;
        align-items: center;
        flex-direction: column;
        
    }

 


    .flex{
        display: block;
        max-width: 933px;
        max-height: 700px;
        margin: 20px auto;
  

        .ViostreamIframe {
            position:absolute; top:0; left: 0
        }

    
       
        


        &__item{

           padding: 100px 20px;


           &--1{          
            max-width: 650px;
            height: auto;

           }
           &--2{
            max-width: 450px;
            height: auto;
           }
        }
    }

    
 

    @media only screen and (max-width: 1000px){
        h1 {
            padding-bottom: 50px;
        }
        .flex {
            flex-direction: column;
            align-items: center;

            &__item{
                max-width: 100%;
                padding: 20px 100px;
            }

        }
        
    }
    @media only screen and (max-width: 1120px){

    
        .flex {
            flex-direction: column;
            align-items: center;

            &__item{
                max-width: 100%;
                padding: 20px 20px;
            }

        }
    }



   
}