.dev {

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    form {
        padding: 30px 0;
        display: flex;
        flex-direction: column;
        justify-content: center;


        
    }

    input {
            background-color: $blue;
            border: 2px solid $blue2;
            color: $white;
            padding: 15px;
            font-size: 20px;
            text-align: center;
            margin: 20px auto 0 auto;
            width: 80%;
        }



    &__item {
        width: 60%;
        background-color: $blue;
        height: 60px;
        font-size: 20px;
        border-radius: 10px;
        color: $white;
        border: 2px solid $blue2;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 5px;

 

        &--1{
            display: none
        }

        
        &--2{
            display: none
        }
        &--3{
            display: none
        }
    }

    @media only screen and(max-width: 700px) {

        
        &__item {
            width: 100%;
         
    
     
      }
    }
}