    // Font Smoothing

    body{
        -webkit-font-smoothing : antialiased; // Chrome, Safari
        -moz-osx-font-smoothing: grayscale;   // Firefox
    }
    

    
    h1, .h1 {
            font-family: "Open Sans", sans-serif;
          font-size: 40px;
          line-height: 42px;
          color: rgb(0, 0, 0);
          font-weight: 600;
          font-style: normal;
          text-align: center;
          width: 50%;
          margin: 0 auto;
      
        }
        h2, .h2 {
            font-family: "Open Sans", sans-serif;
          font-size: 25px;
          line-height: 27px;
          color: rgb(0, 0, 0);
          font-weight: 400;
          font-style: normal;
          text-align: center;
          width: 100%;
          margin: 0 auto;
      
        }

        p{
            font-family: "Open Sans", sans-serif;
            font-size: 16px;
            margin-top: 20px;

        }
    
    
    a {
        color: $white;
        text-decoration: none;
    }
    
    .bold {
        font-weight: 700;
    }
  
    @media only screen and(max-width: 1000px) {
    h1, .h1 {
        font-size: 35px;
        line-height: 36px;
    }
    }
    @media only screen and(max-width: 700px) {
        h1, .h1 {
            font-size: 30px;
            line-height: 31px;
        }

        @media only screen and(max-width: 630px) {
            h1, .h1 {
                width: 80%;
            }
        }
        @media only screen and(max-width: 500px) {
            h1, .h1 {
                font-size: 25px;
            line-height: 26px;
            }
        }
    }
