


html {
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
}

body {
    font-family:  Arial, sans-serif;
    font-size: 16px;
    color: #000;
    background-color: rgba(153, 145, 145, 0.39);
}
header, nav, main, aside, footer, article, section {
    display: block;
  }


